* {
	box-sizing: border-box; //задаваём размеры не контента, а размеры блока
	-webkit-box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: Roboto; }

.modal {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 100000;
	&_fon {
		background: #F6FCFF;
		opacity: .8;
		position: fixed;
		width: 100%;
		height: 100%; }
	&_modaltext {
		color: #000;
		font-size: 30px;
		padding: 20px 40px;
		border: 2px solid #AEAEAE;
		background: #F6FCFF;
		position: fixed;
		top: 80px;
		left: 50%;
		margin-left: -30%;
		width: 60%;
		box-shadow: 0 4px 10px 2px rgba(0,0,0,0.5); }
	&_closeblock {
		cursor: pointer;
		position: fixed;
		line-height: 60px;
		font-size: 82px;
		transform: rotate(45deg);
		text-align: center;
		top: 20px;
		right: 30px;
		color: #337AB7;
		&:hover {
			color: #000; } } }

.container {
	width: 1140px;
	margin: 0 auto;
	position: relative; }

.main {
	height: 800px;
	padding: 39px 0 62px 0;
	background: url("../src/img/main-fon.jpg") center center/cover no-repeat;
	&_text {
		font-size: 21px;
		font-weight: 300;
		text-align: center;
		color: #ffffff;
		margin-top: 23px; }
	span {
		font-weight: bold;
		color: #f9bf3b; }
	&_btn {
		display: block;
		width: 313px;
		height: 72px;
		margin: 0 auto;
		margin-top: 73px;
		border-radius: 4px;
		box-shadow: inset 0 0 0 rgba(0, 0, 0, 0.18);
		border: solid 4px rgba(0, 0, 0, 0.23);
		background-image: linear-gradient(to top, #2798b9, #3ab6da);
		text-shadow: -0.7px -0.7px 0 rgba(0, 0, 0, 0.22);
		font-size: 18px;
		text-align: center;
		color: #ffffff;
		cursor: pointer;
		outline: none;
		text-transform: uppercase; } }

.logo {
	&_img {
		display: block;
		margin: 0 auto;
		width: 83px;
		height: 71px; }
	&_text {
