	text-transform: uppercase;
	line-height: 0.59; }

.big_text {
	font-size: 80px;
	font-weight: 900;
	text-align: center;
	text-transform: uppercase;
	line-height: 0.9;
	overflow: hidden; // Отображается только область внутри элемента, остальное будет скрыто
	margin: 0 auto;
	margin-top: 21px;
	margin-bottom: 30px;
	position: relative;
	span {
		color: #ffffff; }
	& span:before {
		display: block;
		width: 295px;
		content: ''; // позволяет вставлять генерируемое содержание в текст страницы, которое первоначально в тексте отсутствует
		position: absolute;
		border-bottom: 2px solid;
		top: 50%;
 }		// right: 520px
	& span:after {
		display: block;
		width: 295px;
		content: '';
		position: absolute;
		border-bottom: 2px solid;
		top: 50%;
		right: 0px; } }

